:root {
    --background-color: #fff;
    --shadow-color: rgb(0 0 0 / 0.25);
    --border-color: #eee;
    --button-color: #125082;
    --color1: #fff;
    --color2: #c5c5c5;
    --color3: #9e9e9e;
    --color4: yellow;
    --color5: #f5f5f5;
  
    --border: 1px solid var(--border-color);
  }
  
  /* general */
  
  .rgt-text-truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .rgt-clickable {
    cursor: pointer;
  }
  
  .rgt-disabled {
    cursor: not-allowed;
  }
  
  .rgt-disabled-button {
    background: var(--color2) !important;
    cursor: not-allowed !important;    
  }
  
  .rgt-flex-child {
    flex: 1;
  }
  
  /* .rgt-row-even {
    background: #fbfbfb !important;
  }
  
  .rgt-row-hover {
    background: #f5f5f5 !important;
  } */
  
  /* elements */
  
  .rgt-wrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .rgt-container {
    display: inline-grid;
    background: var(--background-color);
    width: 100%;
    height: 100%;
    overflow: auto;
    transition: all .1s ease;
  }
  
  .rgt-cell {
    background: var(--background-color);
    display: flex;
    height: 100%;
    align-items: center;
    border-bottom: var(--border);
    min-height: 48px;
  }
  
  .rgt-cell-inner {
    margin: 0 20px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
  }
  
  .rgt-cell-header {
    display: flex;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 100%;
    z-index: 1;
    min-height: 48px;
    max-height: 48px;
    border-bottom: var(--border);
  }
  
  .rgt-cell-header-virtual-col {
    border-bottom: var(--border);
    background: var(--background-color);
  }
  
  .rgt-cell-header-inner {
    padding: 0 20px;
    display: flex;
    flex: 1;
    align-items: center;
    width: 100%;
    background: var(--background-color);
  }
  
  .rgt-cell-header-inner-not-pinned-right {
    border-right: var(--border);
    margin-right: 1px; 
  }
  
  .rgt-cell-header-inner-checkbox-column {
    padding: 0px;
  }
  
  .rgt-resize-handle {
    height: 100%;
    width: 10px;
    z-index: 1;
    cursor: w-resize;
    position: absolute;
    top: 0;
    right: 0;
  }
  
  .rgt-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    font-weight: 500;
    background: var(--background-color);
    z-index: 1;
    border-top: var(--border);
    overflow-x: auto;
    min-height: 49px;
  }
  
  .rgt-footer-items-information {
    padding: 12px 20px;
    white-space: nowrap;
  }
  
  .rgt-footer-items-per-page {
    cursor: pointer;
    margin-right: 20px;
    margin-left: 10px;
    border-radius: 4px;
    border-color: var(--border-color);
  }
  
  .rgt-footer-page-input {
    padding: 0px 0px 0px 5px;
    outline: none;
    flex: 1;
    max-width: 52px;
    line-height: 22px;
    margin: 0 10px -2px;
    border-radius: 4px;
    border: var(--border);
  }
  
  .rgt-footer-right-container {
    display: inline-flex;
    align-items: center;
    padding: 12px 20px;
    white-space: nowrap;
  }
  
  .rgt-footer-pagination-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0px 10px 0 20px;
  }
  
  .rgt-footer-pagination-button { 
    background: var(--button-color);
    color: var(--color1);
    margin-left: 10px;
    border: none;
    border-radius: 4px;
    padding: 0px 12px;
    cursor: pointer;
    display: block;
    min-height: 24px;
    max-height: 24px;
    min-width: 60px;
    outline: none;
    position: relative;
    box-shadow: 1px 1px 1px 0px var(--shadow-color);
    font-size: 12px;
  }
  
  .rgt-header-checkbox-cell {
    padding: 0 16px;
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
  }
  
  .rgt-cell-checkbox {
    padding: 0 16px;
    box-sizing: border-box;
    justify-content: center;
    background: var(--background-color);
  }
  
  .rgt-sort-icon {
    font-size: 9px; 
    margin-left: 7px;
  }
  
  .rgt-no-data-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 36px;
    font-weight: 700;
    color: var(--color3);
  }
  
  .rgt-column-sort-ghost {
    border-left: var(--border);
    border-right: var(--border);
    z-index: 2;
  }
  
  .rgt-header-container {
    padding: 10px 20px;
    display: flex;
    width: 100%;
    background: var(--background-color);
    align-items: center;
    justify-content: space-between;
    border-bottom: var(--border);
  }
  
  .rgt-search-highlight {
    background: var(--color4);
  }
  
  .rgt-columns-manager-wrapper {
    position: relative;
    z-index: 3;
    display: inline-flex;
  }
  
  .rgt-columns-manager-button {
    cursor: pointer;
    height: 29px;
    width: 29px;
    padding: 2px;
    background: rgb(243, 243, 243);
    outline: none;
    border-radius: 50%;
    border: none;
    box-shadow: 1px 1px 2px 0px var(--shadow-color);
  }
  
  .rgt-columns-manager-popover {
    display: inline-flex;
    flex-direction: column;
    transition: transform .1s ease-out;
    transform-origin: top right;
    transform: scale(0);
    padding: 10px 0px;
    position: absolute;
    right: 50%;
    top: 80%;
    background: var(--background-color);
    width: 200px;
    border-radius: 2px;
    box-shadow: 1px 1px 4px 0px var(--shadow-color);
  }
  
  .rgt-columns-manager-popover-open {
    transform: scale(1);
  }
  
  .rgt-columns-manager-popover-row {
    display: flex;
    flex: 1;
    justify-content: space-between;
    padding: 5px 20px;
    font-size: 14px;
  }
  
  .rgt-columns-manager-popover-row:hover {
    background: var(--color5);
  }
  
  .rgt-search-container {
    z-index: 1;
    flex: 1;
    display: inline-flex;
  }
  
  .rgt-search-label {
    line-height: 30px;
    font-weight: 500;
    font-size: 16px;
    margin-right: 5px;
    display: inline-flex;
    align-items: center;
  }
  
  .rgt-search-icon {
    font-size: 22px;
    transform: rotate(-35deg);
    display: inline-block;
    margin-right: 5px;
    color: var(--color2);
  }
  
  .rgt-search-input {
    line-height: 30px;
    margin-right: 10px;
    width: 100%;
    border: none;
    outline: none;
    font-size: 16px;
    padding: 0;
  }
  
  .rgt-cell-editor-inner {
    position: relative;
    height: 30px;
    width: 100%;
  }
  
  .rgt-cell-editor-input {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    border: none;
    border-bottom: var(--border);
    outline: none;
    font-size: 16px;
    padding: 0;
    font-family: inherit;
  }
  
  .rgt-cell-header-sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }
  
  .rgt-cell-header-pinned {
    z-index: 2;
  }
  
  .rgt-cell-header-pinned-left {
    left: 0;
  }
  
  .rgt-cell-header-pinned-right {
    right: 0;
  }
  
  .rgt-cell-pinned {
    position: -webkit-sticky;
    position: sticky;
    z-index: 1;
  }
  
  .rgt-cell-pinned-left {
    left: 0;
  }
  
  .rgt-cell-pinned-right {
    right: 0;
  }